:root {
    --bs-border-color: var(--tc-system-stroke) !important;
    --bs-primary-rgb: 26, 170, 89;

    .bg-primary-2 {
        background-color: var(--tc-system-radish);
    }

    .text-primary-2 {
        color: var(--tc-system-radish);
    }

    .text-secondary-2 {
        color: #f1f3f8 !important;
    }

    .bg-secondary-2 {
        background: #f1f3f8 !important;
    }
}

// background colors
.bg-light {
    background-color: var(--tc-bg-light) !important;
}
// text colors
.text-dark {
    color: var(--tc-system-black-2) !important;
}
.text-yellow {
    color: var(--tc-system-yellow) !important;
}

.btn-text {
    font-family: var(--tc-btn-font-family);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    /* 14px */
    text-transform: uppercase;
}

.fill-primary {
    path {
        fill: var(--tc-theme-primary) !important;
    }

    fill: var(--tc-theme-primary) !important;
}

.stroke-primary {
    path {
        stroke: var(--tc-theme-primary) !important;
    }

    circle {
        stroke: var(--tc-theme-primary) !important;
    }
}
// variables end
.backdrop-filter-20 {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(20px);
}

.backdrop-filter-50 {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(50px);
}

.mask-image {
    mask-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 1) 20%, rgba(0, 0, 0, 1) 80%, rgba(0, 0, 0, 0));
}

.mask-image-2 {
    mask-image: linear-gradient(-270deg, #fff 14.77%, rgba(255, 255, 255, 0) 100%);
}

.bg-dark {
    --bs-dark-rgb: 41, 41, 41;
}

.bg-dark-2 {
    background: var(--tc-system-black-2);
}

.filter-invert {
    filter: invert(1);
}

.contact-map {
    max-height: 700px;

    iframe {
        height: 700px;
        width: 100%;
    }
}

.map {
    -webkit-filter: grayscale(75%);
    -moz-filter: grayscale(75%);
    -ms-filter: grayscale(75%);
    -o-filter: grayscale(75%);
    filter: grayscale(75%);
}
